___CSS_LOADER_EXPORT___.push([module.id, `@font-face {
    font-family: 'myfont';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    font-style:normal;
}
:root{
    --navercolor: rgb(100, 241, 100);
    --layoutcolor: rgba(196, 192, 192, 0.671);
}

html{
    font-family: 'myfont';
    margin:0;
}

body{
    margin:0;
    padding:0;
}

@supports (height: 100vh) { 
    body{ 
      background: linear-gradient(to right top, var(--navercolor) 50%, white 50%);
      background-size: 100% calc(100% - 100vh + 128px);
      background-repeat: no-repeat;
    }
  
    body:before{
      content:'';
      position: fixed;
      top: 128px;
      bottom: 0;
      width: 100%;
      z-index: -1;
      background: white;
    }
  }

header{
    border-bottom: 1px solid var(--layoutcolor);
    position:fixed;
    top:0;
    width:100%;
    height:124px;
    background-color: white;
}
h1{
    font-size: 3rem;
}
header>h2,h1{
    margin:0;
    text-decoration:var(--layoutcolor) underline;
    padding-left:2rem;
    color:var(--navercolor);
}
header>div{
    display:flex;
    align-self: stretch;
    justify-content: space-evenly;
    align-items: center;
    position: absolute;
    bottom:0;
    width: 100%;
}
header p{
    margin:0;
    font-size: 1.5srem;
    color:var(--layoutcolor);
}
header p:hover{
    cursor: pointer;
}
main{
    display:grid;
    margin-top:128px;
    grid-template:60px 1fr 1fr/1fr auto 1fr;
}
#content{
    height:60vh;
    width:60vw;
    border: 1px solid var(--layoutcolor);
    grid-column: 2/3;
    grid-row:2/3;
}
#sidebar{
    border-radius: 1rem;
    border: 1px solid var(--layoutcolor);
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    position:fixed;
    right:0;
    top:256px;
}
#sidebar a{
    border-bottom: 1px solid var(--layoutcolor);
    text-align: center;
    padding:20px;
}
#sidebar a:nth-last-child(1){
    border-bottom: none;
}
.sideImage{
    width:40px;
    border-radius: 5px;
}
footer{
    background-color: black;
    color:white;
    height: 2rem;
    text-align: center;
}
footer p{
    padding: 0;
    margin:0;
}
